const getters= {
  getCustomers(state){
    return state.customers;
  }
}
const mutations= {
  
  setNewCustomer(state,payload){
      console.log(payload);
      
    state.customersAll.unshift(payload)
    console.log(state.customersAll);
    
  //  state.customersAll.unshift(payload)
  },
  setCustomersForBranch(state,payload){
    state.customers=[]  
    console.log(payload,'work');
   state.customersAll.filter(response=>{
    if(payload.branch.branchName == 'All')
    {
      state.customers = state.customersAll
    }
    else  if(response.branch == payload.branch.branchName && response.isActive)
    {
      state.customers.push(response)
    }
   })

    
  }
}

const actions= {
  sendNewCustomer(VuexContext,payload){
   
    VuexContext.commit('setNewCustomer', payload)
    console.log(payload);
    
}
}

export default{
    state,
    getters,
    mutations,
    actions
}