<template>
  <b-modal size="xl" title="Free Artikel" hide-footer id="getLagerFreeProduct" ref="getLagerFreeProduct">
  {{index}} {{branchId}}
   <div class="text-right my-3">
               <button @click="sendItemsInLager()" :disabled="multipleSelection.length==0" class="btn btn-outline-primary">Hinzufügen</button>

   </div>

       <el-table
      class="border shadow p-3" 
      :data="getFreeBranchProduct"
       ref="multipleTable"
    @selection-change="handleSelectionChange"
      style="width: 100%">
          <el-table-column
      type="selection"
      width="55">
    </el-table-column>
      <el-table-column prop="artikelNum" label="Artikel Nr." sortable> </el-table-column>
      <el-table-column prop="productName" label="Artikel"> </el-table-column>
      <el-table-column
        prop="belegNo"
        label="Beleg Nr."
      >
      </el-table-column>
      <!-- <el-table-column prop="quantity" label="Menge"> </el-table-column> -->
      <el-table-column prop="isEdit" label="Edit">
                          <template slot-scope="scope">
                    <i style="cursor:pointer;font-size:24px" @click="removeItem(scope.$index)" class="text-danger fas fa-minus-circle"></i>
                </template> </el-table-column>

       </el-table>
  </b-modal>
</template>

<script>
import { mapGetters } from 'vuex'
import {cryptoData , decryptoData} from '../../../secure'
export default {
    props:['index','branchId'],    
    computed:{
        ...mapGetters(['getFreeBranchProduct'])
    },
    data(){
        return {
                multipleSelection: [],
                multipleId :0,
        }
    },
    methods:{
        removeItem(index)
        {
            this.$store.dispatch('removeFreeBranchItem',index)
        },
        sendItemsInLager(){
          console.log(this.index);
            let index =[]
            for (let i = 0; i < this.getFreeBranchProduct.length; i++) {
               for (let j = 0; j < this.multipleSelection.length; j++) {
                 if(this.getFreeBranchProduct[i].id==this.multipleSelection[j].id){
                   index.push(i)
                 }
                 
               }
              
            }
            console.log(index);
            index.sort()
            index.reverse()
            this.$store.dispatch('sendItemsInLager',{items : this.multipleSelection, index: index, branchId : this.branchId , dataIndex : this.index})
                    setTimeout(() => {
            this.$notify({
          title: "Erfolg",
          message: "Das Produkt wurde erfolgreich hinzugefügt.",
          type: "success"
        });
        }, 150);
        this.$refs['getLagerFreeProduct'].hide();
        },
          toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      }

    },
    created(){
      let item=[]
      console.log('girdi', this.getFreeBranchProduct);
      
    
        if(this.getFreeBranchProduct.length==0){
      console.log('girdi');

    //    item = JSON.parse(localStorage.getItem('freep'))
     item = decryptoData(localStorage.getItem('freep'))
      item = JSON.parse(item)
        console.log(item);
        
        if(item!=null){
          this.$store.commit('setInitFreeBranchProduct',item)
        }
      }
      
    }

}
</script>

