<script>
import { dateFormatterElement, dateFormatter } from "../../../dateFormatter";
import newProductWei from "../newProductModalItems/newProductWeißeware";
import newProductBraun from "../newProductModalItems/newProductBrauneware";
import { mapGetters } from 'vuex';
const cityOptions = [
  "Ausbau AG 10, -Euro",
  "Aufbau Waschtrockensaeule",
  "Tueranschlagwechsel bis 85cm"
];
const cityOptionsPremium = [
  "Unterbau Geschirspueler",
  "Unterbau Kuehlschrank",
  "Unterbau Waschmaschiene",
  "Tueranschlag wechsel",
  "Lieferung Side by Side",
  "Anschluss Standherd",
  "Anschluss Einbauherd",
  "Einbau Geschirspueler",
  "Einbau Kuehlschrank",
  "Einbau Waschmaschiene",
  "Montage Dunstabzugshaube"
];
const options = [
  {
    value: "8-12",
    label: "8-12"
  },
  {
    value: "10-14",
    label: "10-14"
  },
  {
    value: "12-16",
    label: "12-16"
  },
  {
    value: "14-18",
    label: "14-18"
  }
];
const optionsExpress = [
  {
    value: "10-11",
    label: "10-11"
  },
  {
    value: "11-12",
    label: "11-12"
  },
  {
    value: "12-13",
    label: "12-13"
  },
  {
    value: "13-14",
    label: "13-14"
  },
  {
    value: "14-15",
    label: "14-15"
  },
  {
    value: "15-16",
    label: "15-16"
  },
  {
    value: "16-17",
    label: "16-17"
  }
];
export default {
  props: ["editCustomer"],
  components: {
    newProductWei,
    newProductBraun
  },
  computed:{
    ...mapGetters(['getActiveUser'])
  },
  data() {
    return {
      edit: false,
      form: {
        belegNo: "",
        name: "",
        surname: "",
        address: "",
        plz: "",
        telefon: ""
      },
      optionsSelect: cityOptions,
      checkAll: false,
      checkAllPremium: false,
      checkedCities: [],
      checkedCitiesPrmeium: [],
      cities: cityOptions,
      optionsPremium: cityOptionsPremium,
      isIndeterminate: true,
      isIndeterminatePremium: true,
      chooseLager: "Außenlager",
      selectedTime: "",
      OldAppliance: false,
      selectedDate: "",
      selectedType: "Komfort",
      optionsType: [
        { value: "Komfort", label: "Komfort" },
        { value: "Premium", label: "Premium" }
      ],

      value2: "",
      resultOptions: options,
      expresslieferung: false,
      goodsType1: false,
      goodsType2: false,
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() <= Date.now();
        },
        shortcuts: [
          {
            text: "Heute",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "Morgen",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "nächste Woche",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      }
    };
  },
  watch: {
    editCustomer() {
      if (this.editCustomer.department != "Brauneware") {
        this.goodsType1 = true;
        this.goodsType2 = false;
      } else if (this.editCustomer.department == "Brauneware") {
        this.goodsType1 = false;
        this.goodsType2 = true;
      }
      if (this.editCustomer.express) {
        this.resultOptions = optionsExpress;
        this.selectedTime = "";
      } else this.resultOptions = options;
      this.selectedTime = "";
    },
    "editCustomer.express"() {
      if (this.editCustomer.express) {
        this.resultOptions = optionsExpress;
        this.selectedTime = "";
      } else this.resultOptions = options;
      this.selectedTime = "";
    },
    selectedType() {
      this.selectedType == "Premium"
        ? (this.optionsSelect = cityOptionsPremium)
        : (this.optionsSelect = cityOptions);
    },
    value2(event) {
      console.log(event);
      if (event != null) {
        this.selectedDate = dateFormatterElement(event);
        this.selectedDate = dateFormatter(this.selectedDate);
      } else {
        this.selectedDate = "";
      }
    }
  },
  methods: {
    handleCheckAllChange(val) {
      this.selectedType == "Premium"
        ? (this.optionsSelect = cityOptionsPremium)
        : (this.optionsSelect = cityOptions);
      this.checkedCities = val ? this.optionsSelect : [];
      this.isIndeterminate = false;
    },
    handleCheckedCitiesChange(value) {
      let checkedCount = value.length;
      this.checkAll = checkedCount === this.cities.length;
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.cities.length;
    }
  }
};
</script>

