<template>
  <b-modal
    id="newLagerProduct"
    ref="newLagerProduct"
    hide-footer
    size="md"
    title="Artikel Hinzufügen"
  >
    <eralp-form @inputVModel="artikelNum = $event"> Artikel Suchen </eralp-form>
    <el-card
      shadow="always"
      class="my-2"
      v-for="(product, index) in filtredGetProduct"
      :key="index"
    >
      <div v-b-modal.newProcutModal @click="addCountGoods(product)">
        <el-row>
          <el-col :span="12"
            ><strong style="font-weight:700"
              >Artikel Num : {{ product.artikelNum }}
            </strong></el-col
          >
          <el-col :span="12">{{ product.productName }}</el-col>
        </el-row>
      </div>
    </el-card>
    <b-modal ref="newProcutModal" id="newProcutModal" hide-footer title="Neu">
            <div class="eralp-form-control">
      <div class="eralp-input-container">
        <input v-model="newGoodsItems.artikelNum" disabled  type="text" required />
        <label> </label>
      
      </div>
    </div>
           <div class="eralp-form-control">
      <div class="eralp-input-container">
        <input v-model="newGoodsItems.productName" disabled  type="text" required />
        <label>  </label>
      
      </div>
    </div>
          <label for="">Menge</label>
          <input type="number" class="form-control" v-model="quantity">

          <div class="text-center my-3">
              <button @click="sendFreeProduct()" v-b-modal.freeProducts class="btn btn-outline-primary mx-3">Speichern</button>
              <button @click="$refs['newProcutModal'].hide()" class="btn btn-outline-primary">Abbrechen</button>
          </div>
          <b-modal id="freeProducts" ref="freeProducts" hide-footer title="Free Artikel" size="xl">
<div class="my-2 text-right">
        <el-button  type="primary" v-b-modal.newLagerProduct @click="$refs['newLagerProduct'].hide()" round> <i class="fas fa-plus"></i>  Artikel Hinzufügen</el-button>
        <el-button  type="primary" v-b-modal.newBelegNum round> <i class="fas fa-plus"></i>  Beleg Speichern</el-button>
</div>
{{branchId}} {{index}} {{branchName}}
                     <el-table

      class="border shadow p-3"
      :data="getFreeProduct"
      style="width: 100%"
    >
      <el-table-column prop="artikelNum" label="Artikel Nr." sortable> </el-table-column>
      <el-table-column prop="productName" label="Artikel"> </el-table-column>
      <el-table-column prop="quantity" label="Menge"> </el-table-column>
      <el-table-column prop="isEdit" label="Edit"> </el-table-column>

       </el-table>
       <b-modal id="newBelegNum" ref="newBelegNum" title="New Beleg Nr." hide-footer>
           <eralp-form @inputVModel="newBelegNumber=$event">Beleg Nr.</eralp-form>
           <el-card shadow="always" class="text-center">Filiale : {{branchName}}</el-card>

           <div class="my-3 text-center">
               <button @click="sendNewItemsForLagerInBranch()" class="btn btn-outline-primary btn-sm">Speichern</button>
           </div>
       </b-modal>
          </b-modal>
    </b-modal>
  </b-modal>
</template>

<script>
import eralpForm from "../../eralpComponents/eralp-form";
import { mapGetters } from "vuex";
export default {
  components: {
    eralpForm
  },
    props:['index','branchId','branchName'],
  computed: {
    ...mapGetters(["getGoods", "getLagers",'getFreeProduct','getProductsBelegNum','getId']),
    filtredGetProduct() {
      return this.getGoods.filter(res => {
        return String(res.artikelNum).match(this.artikelNum);
      });
    }
  },
  data() {
    return {
      artikelNum: null,
      newGoodsItems: [],
      quantity:0,
      newBelegNumber:null
    };
  },
  methods: {
    addCountGoods(product) {
        this.quantity=0
      let count = 0;
      this.getGoods.forEach(response => {
        if (response.artikelNum == product.artikelNum) {
          count++;
        }
      });
      this.newGoodsItems = { ...product, count: count };
      console.log(this.newGoodsItems);
    },
    sendFreeProduct(){
        this.$store.dispatch('sendFreeProduct',{...this.newGoodsItems, quantity: this.quantity})
    },
    sendNewItemsForLagerInBranch(){
              let date= new Date();
              console.log(date);
              let data = []
              let createdAt = String(date).substr(8,16)
              for (let i = 0; i < this.getFreeProduct.length; i++) {
                 for (let j = 0; j < this.getFreeProduct[i].quantity; j++) {
                  this.$store.commit('setId')
                    data.push({...this.getFreeProduct[i],id:this.getId})
                 }
                
              }
                this.$store.dispatch('sendGoodsForProductsBelegNum', {  items : data, createdAt: createdAt, index : this.index, branchId : this.branchId, belegNo : this.newBelegNumber})
        this.$store.dispatch('sendNewItemsForLagerInBranch', { einausIndex: this.getProductsBelegNum.length, items : data, index : this.index, branchId : this.branchId, belegNo : this.newBelegNumber})

                           setTimeout(() => {
            this.$notify({
          title: "Erfolg",
          message: "Das Produkt wurde erfolgreich hinzugefügt.",
          type: "success"
        });
        }, 150);
        this.$refs['newLagerProduct'].hide();

    }
  }
};
</script>

