<template>
  <div>
    <h3 class="text-center text-white bg-danger">BU SAYFADA TÜM İŞLEVLER ÇALIŞIYOR ! SAĞ ALTAKİ SARI HELPER BUTONU İLE DEPODAKİ RAFLAR DİNAMİK ŞEKİLDE DEĞİŞMEKTEDİR.</h3>
    {{branchId}}
    <span
      v-if="selectedData.length != 0"
      style="position:fixed;right:10px;z-index:1050"
      :style="{ width: '350px!important', bottom: '20px' }"
    >
    <transition-group enter-active-class="animated slideInRight">
      <li 
         draggable="true" @dragstart="dragStart(index,$event)" 
        v-for="(data, index) in selectedData"
        style=" cursor: grab;list-style: none;"
        :key="index"
        class="my-2 parent border shadow p-3 bg-light rounded" 
        >
        <el-row >
          <el-col :span="8">
            {{ data.artikelNum }}
          </el-col>
          <el-col :span="8"> {{ data.productName.substr(0, 8) }}.. </el-col>
          <el-col :span="4">
            <i
            @click="closeDragItems(data,index)"
              style="cursor:pointer;font-size:24px"
              class="text-danger fas fa-minus-circle"
            ></i>
          </el-col>
        </el-row>
      </li>
      </transition-group>
    </span>
    <el-row>
      {{ branchId }} {{ branchName }} 
      <el-col :span="12"
        ><h2>Filiale : {{ branchName }}</h2></el-col
      >
      <el-col v-if="getActiveUser.isLagerschreiben" :span="12">
        <el-button type="primary" v-b-modal.newLagerProduct round>
          <i class="fas fa-plus"></i> Artikel Hinzufügen</el-button
        >
      </el-col>
    </el-row>
    <div
@dragover.prevent @dragenter="dragEnter" @dragleave="dragLeave" @dragend="dragEnd" @drop="dragFinish(index, $event)"
       :class="over? 'animated pulse infinite bg-lightsuccess' : ''"
      class="aspect-tab"
      v-for="(lager, index) in getBranchLager"
      :key="index"
    >
  
      <input
        :id="'item-' + index"
        type="checkbox"
        class="aspect-input"
        name="aspect"
      />
      <label :for="'item-' + index" class="aspect-label"></label>
      <div class="aspect-content text-left">
        <div class="aspect-info">
          <el-progress
            :width="60"
            :stroke-width="4"
            type="dashboard"
            :percentage="lager.allGoods * 5"
            :color="colors"
          ></el-progress>
          <span class="aspect-name"
            >Stellplatz {{ lager.id }} - Gang {{Math.ceil(lager.id/12)}}</span
          >
        </div>
      </div>
      <div class="aspect-tab-content">
        <div  v-if="getActiveUser.isLagerschreiben" class="text-center">
          <el-button
            class="my-2"
            @click="getLagerIndex(index)"
            v-b-modal.getLagerFreeProduct
            type="success"
            round
          >
            <i class="fas fa-plus"></i> Artikel Hinzufügen</el-button
          >
        </div>
     
        <div
        
           style="cursor:pointer;"
          @dblclick="getActiveUser.isLagerZua ? dragData(data,indexData,index):''"
       
          class="bg-white p-3 my-2"
          v-for="(data, indexData) in lager.data"
          :key="indexData"
        >
          <div class="opinion-header">
            <span style="font-weight:800"
              >Artikelnummer : {{ data.artikelNum }}</span
            >
          </div>
          <div>
            <span>{{ data.productName }}</span>
          </div>
        </div>
      </div>
    </div>
    <div class="text-center">
        <el-button
            class="my-2"
            @click="newStellplatz()"
            type="success"
            round
          >
            <i class="fas fa-plus"></i> Neu Stellplatz</el-button
          >
    </div>
    <new-lager-product-modal
      :branchName="branchName"
      :branchId="branchId"
      :index="index"
    ></new-lager-product-modal>
    <get-lager-free-product-modal
      :branchId="branchId"
      :index="index"
    ></get-lager-free-product-modal>
  </div>
</template>

<script>

import { mapGetters } from "vuex";
import newLagerProductModal from "./addNewLagerProductModal/addNewLagerProductModal";
import getLagerFreeProductModal from "./getLagerFreeProductModal/getLagerFreeProductModal";
export default {
  components: {
    newLagerProductModal,
    getLagerFreeProductModal
  },
  props: ["getBranchLager", "branchName", "branchId"],
  computed: {
    ...mapGetters(["getProducts", "getLagers","getActiveUser","getQuantityProductInLagerForBranch","getProductsBelegNum"])
  },
  data() {
    return {
      percentage: 10,
      colors: [
        { color: "#28d43f", percentage: 20 },
        { color: "#84d428", percentage: 40 },
        { color: "#d4b428", percentage: 60 },
        { color: "#ff5e00", percentage: 80 },
        { color: "#d44a28", percentage: 100 }
      ],
      index: null,
      lagerIndex:null,
      over:false,
      selectedData: [],
      which:null,
      draggingData:[],
      dragStartedItems:[]
    };
  },
  methods: {
    newStellplatz(){
      this.$store.dispatch('sendNewStellplatz',{ branchId : this.branchId, data:[], allGoods:0 })
    },
    getLagerIndex(index) {
      console.log(index);
      
      this.index = index;
    },

    dragData(row,miniIndex,index) {
     
      console.log(index,miniIndex);
        this.lagerIndex = index
      this.selectedData.push({...row, index:index});
      this.getBranchLager[index].data.splice(miniIndex,1)
    },
    closeDragItems(row,index){
          this.selectedData.splice(index,1)
          this.getLagers[row.index].data.push(row)
    },
        dragStart(which, ev) {
      console.log('started',ev,which)
      this.which = which
      this.dragStartedItems = this.selectedData[which]

    },
    dragEnter(ev) {
          this.over=true
          
    },
    dragLeave(ev) {
          this.over=false
    },
    dragEnd(ev) {
       this.over=false
    },
    dragFinish(to, ev) {
      console.log(to);
      
       this.getBranchLager[to].data.push(this.dragStartedItems)
       let lagerId = this.getBranchLager[this.lagerIndex].id
       // STELLPLATZ GUNCELLEME !!!
      let putItem = false
      let index;
      console.log(this.dragStartedItems);
      
        this.getProductsBelegNum.forEach(belegNum=>{
          if(belegNum.belegNo == this.dragStartedItems.belegNo){
            console.log('girdi');
            
             for (let i = 0; i < belegNum.data.length; i++) {
               console.log('fora girdi');
               
               if(belegNum.data[i].artikelNum == this.dragStartedItems.artikelNum && lagerId == belegNum.data[i].stellplatz){
                  putItem = true
                  index = i
                  console.log(index);
                  
               }   
               
             
             }
             if(putItem){
               console.log('girdik');
               
               belegNum.data[index].stellplatz = this.getBranchLager[to].id
             }
          }
        })
       this.selectedData.splice(this.which,1)
          setTimeout(() => {
            this.$notify({
          title: "Erfolg",
          message: "Das Produkt wurde erfolgreich hinzugefügt.",
          type: "success"
        });
        }, 150);
       this.over=false
        
    },
 

  }
};
</script>

<style lang="scss">
$positive-circle: #d44a28;
$negative-circle: #ff5e00;
$circle-gray: #e4e4e4;
$header-black: #242a32;
$background-color: #6fcae7;
$tab-text: #5d5d5d;
$white: #fff;
$reviews-text-black: #363636;
$white-light-gray: #f9f9f9;

@mixin user-select() {
  user-select: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
}

@mixin chart($radius, $width) {
  .chart-pie {
    position: relative;
    display: inline-block;
    height: $radius;
    width: $radius;
    border-radius: 50%;
    background-color: $circle-gray;
    vertical-align: middle;

    &:after {
      content: "";
      display: block;
      position: absolute;
      height: $radius - $width;
      width: $radius - $width;
      top: $width / 2;
      left: $width / 2;
      border-radius: 50%;
      background-color: $white;
 
    }
  }
  .chart-pie-count {
    position: absolute;
    display: block;
    height: 100%;
    width: 100%;
    font-size: 14px;
    font-weight: 500;
    line-height: 44px;
    color: $header-black;
    text-align: center;
    z-index: 1;
  }
  .chart-pie > div {
    clip: rect(0, $radius, $radius, $radius / 2);
  }
  .chart-pie > div,
  .chart-pie.over50 .first-fill {
    position: absolute;
    height: $radius;
    width: $radius;
    border-radius: 50%;
  }
  .chart-pie.over50 > div {
    clip: rect(auto, auto, auto, auto);
  }
  .chart-pie.over50 .first-fill {
    clip: rect(0, $radius, $radius, $radius / 2);
  }
  .chart-pie:not(.over50) .first-fill {
    display: none;
  }
  .bg-lightsuccess{
    background: rgb(199, 255, 199)!important;
  }
  .second-fill {
    position: absolute;
    clip: rect(0, $radius / 2, $radius, 0);
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border-width: $width - 1;
    border-style: solid;
    box-sizing: border-box;
   
  }
  .chart-pie.positive {
    .first-fill {
      background-color: $positive-circle;
    }
    .second-fill {
      border-color: $positive-circle;
    }
  }
  .chart-pie.negative {
    .first-fill {
      background-color: $negative-circle;
    }
    .second-fill {
      border-color: $negative-circle;
    }
  }
}
body {
  background-color: $background-color;
  overflow-y: scroll;
  overflow-x: hidden;
}
#aspect-content {
  margin: 50px 0 0;
  font-family: "Poppins", sans-serif;
  * {
    box-sizing: border-box;
  }
}
.aspect-tab {
  position: relative;
  width: 100%;
  max-width: 1000px;
  margin: 0 auto 10px;
  border-radius: 4px;
  background-color: $white;
  box-shadow: 0 0 0 1px rgba(236, 236, 236, 1);
  opacity: 1;
  transition: box-shadow 0.2s, opacity 0.4s;

  &:hover {
    box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.11);
  }
}
.aspect-input {
  display: none;
}
.aspect-input:checked ~ .aspect-content + .aspect-tab-content {
  max-height: 3000px;
}
.aspect-input:checked ~ .aspect-content:after {
  transform: rotate(0);
}
.aspect-label {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  max-height: 80px;
  width: 100%;
  margin: 0;
  padding: 0;
  font-size: 0;
  z-index: 1;
  cursor: pointer;
  &:hover ~ .aspect-content:after {
    background-image: url("data:image/svg+xml;base64,");
  }
}
.aspect-content {
  position: relative;
  display: block;
  height: 80px;
  margin: 0;
  padding: 0 87px 0 30px;
  font-size: 0;
  white-space: nowrap;
  cursor: pointer;
  @include user-select();
  &:before,
  &:after {
    content: "";
    display: inline-block;
    vertical-align: middle;
  }
  &:before {
    height: 100%;
  }
  &:after {
    position: absolute;
    width: 24px;
    height: 100%;
    right: 30px;
    background-image: url("data:image/svg+xml;base64,");
