}
.positive-count {
  background-image: url("data:image/svg+xml;base64,");
}
.negative-count {
  background-image: url("data:image/svg+xml;base64,");
}
.neutral-count {
  background-image: url("data:image/svg+xml;base64,");
}
.aspect-info {
  width: 60%;
  white-space: nowrap;
  font-size: 0;
  &:before {
    content: "";
    display: inline-block;
    height: 44px;
    vertical-align: middle;
  }
}
@include chart(44px, 4px);
.aspect-tab-content {
  background-color: $white-light-gray;
  font-size: 0;
  text-align: justify;
}
.sentiment-wrapper {
  padding: 24px 30px 30px;
}
.sentiment-wrapper > div {
