<template>
  <div>
    <el-row>
       <el-col class="my-3 text-right" :xs="24" :sm="24" :md="23">
        <el-button type="primary" v-b-modal.newPackageDescription round>Paketbeschreibung Neu</el-button>
      </el-col>
    </el-row>
    <el-table
      class="border shadow p-3"
      :data="getPackageDescriptionTable"
      :default-sort="{prop: 'date', order: 'descending'}"
      style="width: 100%"
    >
      <el-table-column prop="department" label="Abteilung" sortable width="200px"></el-table-column>
      <el-table-column prop="tarif" label="Tarif"></el-table-column>
      <el-table-column prop="description" label="Beschreibung" width="1200px"></el-table-column>
      <el-table-column prop="edit" label="Bearbeiten">
        <template slot-scope="scope">
          <span v-b-modal.editDescription @click="editDesc(scope.$index)">
            <i class="el-icon-edit bg-warning text-dark p-2 rounded-circle" style="font-size:18px"></i>
          </span>
        </template>
      </el-table-column>
    </el-table>
        <b-modal
      id="newPackageDescription"
      ref="newPackageDescription"
      size="xl"
      title="Extra Large Modal"
      hide-footer
    >
      <el-row>
        <el-col :span="12" class="text-center">
          <strong style="font-weight:700">Tarif : </strong>
          <el-select id="label1" v-model="newDesc.tarif" placeholder="Select">
            <el-option
              v-for="item in optionsType"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-col>
        <el-col :span="12" class="text-center">
          <strong style="font-weight:700">Abteilung :  </strong>
          <el-select id="label3" v-model="newDesc.department" placeholder="Select">
            <el-option
              v-for="item in optionsPackage"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-col>
        <el-col :span="22" :offset="1" class="shadow mt-5 border" >
          <textarea  v-model="newDesc.description" name="" class="form-control p-3" id="label2" cols="30" rows="10"></textarea>
        </el-col>
                <el-col :span="22" :offset="1" class="text-right mt-5" >
                  <button @click="sendNewPackageDescription()" class="btn btn-outline-primary">Speichern</button>
        </el-col>
      </el-row>
    </b-modal>
    <b-modal
      id="editDescription"
      ref="editDescription"
      size="xl"
      title="Extra Large Modal"
      hide-footer
    >
      <el-row>
        <el-col :span="12" class="text-center">

          <strong style="font-weight:700">Tarif : </strong>
          <el-select id="label4" v-model="selectedEditDesc.tarif" placeholder="Select">
            <el-option
              v-for="item in optionsType"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-col>
        <el-col :span="12" class="text-center">
          <strong style="font-weight:700">Abteilung :  </strong>
          <el-select id="label5"  v-model="selectedEditDesc.department" placeholder="Select">
            <el-option
              v-for="item in optionsPackage"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-col>
        <el-col :span="22" :offset="1" class="shadow mt-5 border" >
          <textarea id="label6"  v-model="selectedEditDesc.description" name="" class="form-control p-3" cols="30" rows="10"></textarea>
        </el-col>

                <el-col :span="22" :offset="1" class="text-right mt-5" >
                  <button @click="sendEditPackageDescription()" class="btn btn-outline-primary">Speichern</button>
        </el-col>
      </el-row>
    </b-modal>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import auth from '../mixins/auth';
import isGod from '../mixins/isGod';
export default {
  data() {
    return {
      errPoint:[],
      newDesc:{
        tarif : "",
        description: "",
        department : "",

      },
      optionsType: [
        { value: "Standart", label: "Standart" },
        { value: "Komfort", label: "Komfort" },
        { value: "Premium", label: "Premium" },
        { value: "Techn. Hilfe", label: "Techn. Hilfe" }
      ],
      optionsPackage:[
         { value: "Weißware", label: "Weißware" },
         { value: "Brauneware", label: "Brauneware" },
      ],
      selectedEditDesc: []
    };
  },
  mixins:[auth,isGod],
  computed:{
    ...mapGetters(['getPackageDescriptionTable'])
  },
  methods: {
    editDesc(index) {
      console.log(index);
      
      this.selectedEditDesc = this.getPackageDescriptionTable[index];
    },
    sendEditPackageDescription(){
        for (let i = 0; i < this.errPoint.length; i++) {
         $(this.errPoint[i].label).removeClass("borderError")
    }

      this.errPoint=[]
      if(this.selectedEditDesc.tarif==""){
        this.errPoint.push({err:"ungültig Tarif", "label": "#label4"})
      }
      if(this.selectedEditDesc.description==""){
        this.errPoint.push({err: "ungültig Beschreibung", "label": "#label5"})
      }
      if(this.selectedEditDesc.department==""){
        this.errPoint.push({err: "ungültig Abteilung", "label": "#label6"})
      }
      if(this.errPoint.length==0){
        this.$notify({
          title: 'Erfolg',
          message: 'Die Paketbeschreibung wurde erfolgreich aktualisiert.',
          type: 'success'
        });
          this.$refs.editDescription.hide();
      }
      else {
        for (let i = 0; i < this.errPoint.length; i++) {
    setTimeout(() => {
            this.$notify({
          title: "Error",
          message: this.errPoint[i].err,
          type: "error"
        });
        }, i*50);
    $(this.errPoint[i].label).addClass("borderError")
  //  $("p").css("background-color");
  }
      }
    },
    sendNewPackageDescription(){
     for (let i = 0; i < this.errPoint.length; i++) {
         $(this.errPoint[i].label).removeClass("borderError")
    }

      this.errPoint=[]
      if(this.newDesc.tarif==""){
        this.errPoint.push({err:"ungültig Tarif", "label": "#label1"})
      }
      if(this.newDesc.description==""){
        this.errPoint.push({err: "ungültig Beschreibung", "label": "#label2"})
      }
      if(this.newDesc.department==""){
        this.errPoint.push({err: "ungültig Abteilung", "label": "#label3"})
      }
      if(this.errPoint.length==0){
     this.$store.dispatch('pushPackageDescriptionTable',this.newDesc).then(response=>{
        if(response == 'success')
        {
                             this.$notify({
          title: 'Erfolg',
          message: 'Die Paketbeschreibung wurde erfolgreich aktualisiert.',
          type: 'success'
        });
          this.$refs.newPackageDescription.hide();
        }
      })
      }
      else {
        for (let i = 0; i < this.errPoint.length; i++) {
    setTimeout(() => {
            this.$notify({
          title: "Error",
          message: this.errPoint[i].err,
          type: "error"
        });
        }, i*50);
    $(this.errPoint[i].label).addClass("borderError")
  //  $("p").css("background-color");
  }
      }
       
    }
  }
};
</script>

<style>
  .borderError{
  border-left:10px solid red !important;
}
</style>