<template>
  <b-modal id="excellExport" ref="excellExport" size="xl" title="Extra Large Modal" hide-footer>
    <el-row>
      <el-col :span="12" :offset="6">
        <div class="text-center">
          <div class="form-group">
            <strong style="font-weight:700">Datum</strong>
            <el-date-picker
              class="w-100"
              v-model="datePickerElement"
              type="daterange"
              range-separator="To"
              start-placeholder="Start date"
              end-placeholder="End date"
            ></el-date-picker>
          </div>
          <div v-if="getActiveUser!='' ? getActiveUser.isGod ? true: false: false" class="form-group mt-3">
            <el-select v-model="selectedBranch" placeholder="Select">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </div>
          <div v-else class="form-group mt-3">
            {{selectedBranch}}
          </div>
          </div>

      </el-col>
    </el-row>

    <div v-if="selectedBranch!=''">
      <el-table
        class="border shadow p-3"
        :data="filtredTable"
        :default-sort="{prop: 'date', order: 'descending'}"
        style="width: 100%"
      >
        <el-table-column prop="name" label="Vorname" sortable></el-table-column>
        <el-table-column prop="surname" label="Nachname"></el-table-column>
        <el-table-column prop="address" label="Straße" width="400px"></el-table-column>
        <el-table-column prop="beleg" label="Beleg"></el-table-column>

        <el-table-column prop="date" label="Datum"></el-table-column>
        <el-table-column prop="tarif" label="Tarif"></el-table-column>
        <el-table-column prop="department" label="Ware"></el-table-column>
        <el-table-column prop="oldMach" label="Altgerat">
                  <template slot-scope="scope">
    {{scope.row.oldMach? 'Ja':'Nein'}}
      </template>
        </el-table-column>
      </el-table>
              <div v-if="selectedBranch!=''" class="text-center mt-4">
          <vue-excel-xlsx
            :data="filtredTable"
            :columns="columns"
            :filename="selectedBranch"
            :sheetname="selectedBranch"
            class="btn btn-outline-primary"
          >Download</vue-excel-xlsx>
        </div>
    </div>
  </b-modal>
</template>

<script>
import { dateFormatter, dateFormatterElement, dateFormatterConvertNum } from "../../../dateFormatter";
import { mapGetters } from 'vuex';
export default {
  props: ["tableData"],
  data() {
    return {
      datePickerElement: null,
      dateStart: null,
      dateFinish: null,
      dateStartNum: null,
      dateFinishNum: null,
      selectedBranch: "",
      columns:[
         {
          label: "Vorname",
          field: "name"
        },
        {
          label: "Nachname",
          field: "surname"
        },
        {
          label: "Straße",
          field: "address"
        },
         {
          label: "Beleg",
          field: "beleg"
        },
        {
          label: "Tarif",
          field: "tarif"
        },
        {
          label: "Abteilung",
          field: "department"
        }
      ],
      options: [
        {
          value: "PP",
          label: "PP"
        },
        {
          value: "Alex",
          label: "Alex"
        },
        {
          value: "Treptow",
          label: "Treptow"
        },
        {
          value: "EG",
          label: "EG"
        },
        {
          value: "Potsdam",
          label: "Potsdam"
        },
        {
          value: "MZ",
          label: "MZ"
        },
        {
          value: "ESM",
          label: "ESM"
        },
        {
          value: "Köpenick",
          label: "Köpenick"
        },
        {
          value: "GB",
          label: "GB"
        },
        {
          value: "Steglitz",
          label: "Steglitz"
        },
        {
          value: "EPK",
          label: "EPK"
        },
        {
          value: "EPB",
          label: "EPB"
        }
      ]
    };
  },
  watch: {
    datePickerElement() {
      let date1 = dateFormatterElement(this.datePickerElement[0]);
      let date2 = dateFormatterElement(this.datePickerElement[1]);
      this.dateStart = dateFormatter(date1);
      this.dateFinish = dateFormatter(date2);
     this.dateStartNum =  dateFormatterConvertNum(date1)
     this.dateFinishNum =  dateFormatterConvertNum(date2)
    },
    selectedBranch() {
      this.showTable = true;
    }
  },
  computed: {
    filtredTable() {
      return this.tableData.filter(response => {
          let[date,month,year] = response.date.split('.')
          let resultDate = year+'-'+month+'-'+date
          let convertNumDate = dateFormatterConvertNum(resultDate)
            
        if (response.branch == this.selectedBranch && this.dateStartNum < convertNumDate && convertNumDate < this.dateFinishNum) {
          return response;
        }
      });
    },
    ...mapGetters(['getActiveUser'])

  },
  mounted(){
    setTimeout(() => {
    if(this.getActiveUser.length!=0){
        if(!this.getActiveUser.isGod){
      this.selectedBranch = this.getActiveUser.branch.branchName
    }
    }
    }, 1000);
  }
};
</script>

